<template>
  <div class="login-page">
    <div class="main-container container-flex">
      <div class="col-one col50">
        <h2>¡Bienvenido!</h2>
        <p>El éxito es la suma de pequeños esfuerzos día a día.</p>
        <hr>
      </div>
      <div class="col-two col50">
        <div class="col-logo">
          <img src="../assets/logo.jpg">
        </div>
        <div class="form-container">
          <h4>Login</h4>
          <form id="loginapp"   @submit.prevent="login" action="" method="post">  
            <div class="row-block">
              <p for="correo">Correo</p>
              <input 
                  id="correo" 
                  v-model="email_form" 
                  type="email" 
                  name="correo" 
                  required
                  placeholder="info@legrafica.mx"
              >
            </div>
            <div class="row-block">
              <p for="password">Contraseña</p>
              <input 
                  id="password" 
                  v-model="password_form" 
                  type="password" 
                  name="password" 
                  required
                  placeholder="**************"
              
              >
            </div>
            <p v-if="status=='error'" class="error">Has introducido mal el email o la contraseña.</p>
            <div class="btn-login">
              <input type="submit" value="Iniciar Sesión">
            </div>
          </form>
        </div>
        <p class="txt-version">V 1.0.0</p>
      </div>
    </div>
  </div>
</template>
<script>
import userService from "@/services/user.service";

export default {
  name: 'login',
  components: {
  },
  data: ()=>({
    email_form: "",
    password_form: "",
    token: '',
    status:'',
    message:'',
    identity:{ },
  }),
  methods: {
    async login() {

      try {
        // token del usuario identificado
        let response_token =  await userService.login(this.email_form, this.password_form, true);
        if (response_token.status != 'error') {
          this.status = 'success';
          let token = response_token.data.token;

           // let json  = JSON.stringify(this.identity);
           
             //Persistir datos del usuario identificado guardando el token el el localstorage
            userService.setToken(token); 
            
            this.token = userService.getToken() //obtener el token guardado en el localstorage
            this.identity = userService.decode(this.token); //obtener la informacion del usuario decodificando el token
            
            console.log(this.identity);
            console.log(this.token);
                  
            this.$router.push("/"); // redirigir al inicio
            

        } else {
          this.status = 'error';
        }
       
      } catch (error) {
        this.status ='error'
      }
    }
  }
}
</script>


